<template>
	<div id="container">
		<div id="register">
			<h1 style="color: rgb(3, 19, 65)">Registration</h1>
			<input type="text" id="register-ip1" placeholder="Name" v-model="name" />
			<input
				type="email"
				id="register-ip2"
				placeholder="Email"
				v-model="email"
			/>
			<input type="number" id="register-ip3" placeholder="Age" v-model="age" />
			<input
				type="password"
				id="register-ip4"
				placeholder="Password"
				v-model="password"
			/>
			<input
				type="password"
				id="register-ip5"
				placeholder="Password Confirmation"
				v-model="password_confirmation"
			/>

			<button id="register-button" @click="register">Create</button>

			<span style="color: rgb(0, 0, 0)">
				<a>Fogot password</a> |<router-link to="/login"> Sign In </router-link>
			</span>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Register",
		data() {
			return {
				name: "",
				email: "",
				age: "",
				password: "",
				password_confirmation: "",
			};
		},

		methods: {
			register() {
				let newUser = {
					name: this.name,
					email: this.email,
					age: this.age,
					password: this.password,
					password_confirmation: this.password_confirmation,
				};
				const url = "https://apiyowl.herokuapp.com/api/register";
				let requestOptions = {
					method: "post",
					headers: {
						Accept: "application/json",
						"Content-Type": "application/json",
					},
					body: JSON.stringify(newUser),
				};
				fetch(url, requestOptions)
					.then((response) => {
						return response.json();
					})
					.then((result) => {
						localStorage.setItem("RTOKEN", result.token);
					});
				this.$router.push("/login");
			},
		},
	};
</script>

